 @import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap'); 

.App {
  background-color: brown;
  box-sizing: border-box;

}

img {
  height: 1.2cm;
}
.navbar {
  margin: 0px;
  width: auto;
}
ul {
  list-style-type: none;
  display: flex;
  width: 100%;  
  align-items: center;
  top: 0px;
  background-color: #F5FFFF;
} 
li a {
  margin-right: 20px;
  margin-left: 20px;
  display: block;
  font-size: larger;
  /* color: white; */
  text-align: center;
  padding: 20px 20px;
  text-decoration: none;
}

li a:hover {
  background-color: whitesmoke;
}
.search-form {
  min-height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width:30%;
  border:none;
  padding:10px;
}

.search-button {
  background: #D91671;
  border:none;
  padding:10px 20px;
  color:#fff;
}

.recipes-container{
  display: grid;
  grid-template-columns: repeat(2, ifr);
} 
.recipe-body {
  border-radius:10px;
  margin-left: 5px;
  margin-right: 5px;
  float: left;
  background: brown;
  padding: 10px 10px;
} 
.Card{ 
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); 
  text-align: center;
  background-color: brown;
  padding:0 5px;
  display: grid;
  justify-content: center;
  margin-left: 10px;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 5rem;
  
}
.column{
  column-gap: 20px;
  column-rule: 4px; 
  columns: 200px 3;
  float: left;
  padding: 10px 10px;
  margin: 1rem auto;
  display: grid;
}

.image {
  border-radius:50%;
  width: 250px;
  height: 250px;
}

.ingredient-container {
  background: #cce5ff;
  color: darkblue;
  margin: 5px;
  width: 50%;
}
.ingredient {
  background: white;
} 
.Auth-form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.Auth-form {
  width: 420px;
  box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px;
  padding-top: 30px;
  padding-bottom: 20px;
  border-radius: 8px;
  background-color: white;
}

.Auth-form-content {
  padding-left: 12%;
  padding-right: 12%;
}

.Auth-form-title {
  text-align: center;
  margin-bottom: 1em;
  font-size: 24px;
  color: rgb(34, 34, 34);
  font-weight: 800;
  cursor:pointer;
}

label {
  font-size: 14px;
  font-weight: 600;
  color: rgb(34, 34, 34);
}
table {
  width: 80%;
  border-collapse: collapse;
}
td, th {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
  column-width: 100px;
}

tr:nth-child(even){background-color: #f2f2f2;}

tr:hover {background-color: #ddd;}

th {
  /* padding-top: 12px;
  padding-bottom: 12px; */
  text-align: left;
  background-color: #ad6747;
  color: white;
  width: 10%;
} 
.td2 {
  width: 100%;
}
#delete{
  /* display: inline-block; */
  height: 10%;
  color: #fff;
  background-color: #ad6747;
  border-radius: 5px;
  border: solid #cccccc 1px;
  float: left;
}
.footer {
  position:static;
  left: 0;
  bottom: 0;
  width: 100%;
  min-height: min-content;
  /* background-color:#D91671; */
  color: white;
  text-align: center;
}
#submit{
  background-color: #cacace;
}